body {
  background-image: radial-gradient(circle at center, rgb(14, 199, 255) 10%, rgb(119, 15, 255) 90%);
  scroll-behavior: smooth;
}

.container {
  width: 1200px;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;

  text-align: center;
}

form {
  margin-top: 90px;
  margin-bottom: 90px;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

ul {
  display: flex;
  flex-wrap: wrap;
  margin-left: -30px;
  margin-top: -30px;
  list-style: none;
  align-items: center;
  justify-content: center;
}

li {
  flex-basis: calc((100% - 30px * 3) / 3);
  margin-left: 30px;
  margin-top: 30px;
}
.photo-card {
  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2), 0px 5px 5px 2px rgba(0, 0, 0, 0.14),
    3px 5px 7px 3px rgba(0, 0, 0, 0.12);
}

.stats {
  display: flex;
  justify-content: center;
}

.load-more-button {
  margin-top: 90px;
  margin-bottom: 90px;
}

.invisible {
  display: none;
  pointer-events: none;
}
